import React, { useEffect, useState } from "react";
import PageLayout from "../../components/PageLayout";
import PropertiesModal from "./PropertiesModal";
import toast from "react-hot-toast";
import { useWallet } from "../../../packages/provider";
import { NFTContract } from "../../../packages/neo/contracts";
import DisplayRune from "./DisplayRune";
import { IRuneMeta } from "../../../packages/neo/contracts/ftw/nft/interfaces";
import DisplayRandomRune from "../../components/DisplayRandomRune";

const Gallery = (props) => {
  const [tokens, setTokens] = useState<any>([]);
  const [error, setError] = useState("");
  const [propertiesModalActive, setPropertiesModalActive] =
    useState<IRuneMeta>();
  const { connectedWallet, network, addPendingTransaction } = useWallet();
  const onPropertiesModalActive = (obj: IRuneMeta) => {
    if (connectedWallet) {
      setPropertiesModalActive(obj);
    } else {
      toast.error("Please connect wallet.");
    }
  };
  const onMint = async () => {
    if (connectedWallet) {
      try {
        const res = await new NFTContract(network).mint(connectedWallet);
        addPendingTransaction(res);
      } catch (e: any) {
        toast.error(e.message);
      }
    } else {
      toast.error("Please connect wallet.");
    }
  };

  const withdraw = async () => {
    if (connectedWallet) {
      try {
        const res = await new NFTContract(network).deploy(connectedWallet);
        console.log(res);
      } catch (e: any) {
        console.log(e);
        toast.error(e.message);
      }
    } else {
      toast.error("Please connect wallet.");
    }
  };

  useEffect(() => {
    async function fetchContractStatus() {
      setError("");
      try {
        const res = await new NFTContract(network).getTokens();
        setTokens(res);
      } catch (e: any) {
        setError(e.message);
      }
    }
    fetchContractStatus();
  }, [connectedWallet]);
  return (
    <>
      <section className="hero is-white is-medium">
        <div className="hero-body">
          <div className="container">
            <div className="columns">
              <div className="column">
                <div>
                  <h1 className="title">Forthewin Runes</h1>
                  <p className="subtitle">Algorithms-generated lucky runes</p>
                  <div className="field is-grouped is-grouped-multiline mb-5">
                    <div className="control">
                      <div className="tags has-addons">
                        <span className="tag is-dark">Max supply</span>
                        <span className="tag is-black">500</span>
                      </div>
                    </div>
                    <div className="control">
                      <div className="tags has-addons">
                        <span className="tag is-dark">Attribute #1</span>
                        <span className="tag is-black">Phase</span>
                      </div>
                    </div>
                    <div className="control">
                      <div className="tags has-addons">
                        <span className="tag is-dark">Attribute #2</span>
                        <span className="tag is-black">Luck</span>
                      </div>
                    </div>
                  </div>
                  <div className="block has-text-centered-mobile is-hidden-mobile">
                    <button
                      onClick={onMint}
                      className="button is-primary press-font"
                    >
                      Mint: 10 GAS
                    </button>
                  </div>
                </div>
              </div>
              <div className="column">
                <div
                  className="block"
                  style={{ display: "flex", justifyContent: "center" }}
                >
                  <DisplayRandomRune
                    isActive={true}
                    width="250px"
                    height="250px"
                  />
                </div>
                <div className="block has-text-centered-mobile is-hidden-tablet">
                  <button
                    onClick={onMint}
                    className="button is-primary press-font"
                  >
                    Mint: 10 GAS
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
      <PageLayout>
        <div
          style={{
            flexFlow: "wrap",
            // width: "640px",
            // margin: "0 auto"
          }}
          className="is-flex"
        >
          {tokens.map((tokenId) => (
            <DisplayRune
              width={"20%"}
              height={"20%"}
              tokenId={tokenId}
              network={network}
              onClick={onPropertiesModalActive}
            />
          ))}
        </div>
      </PageLayout>
      {/*<button onClick={onMint} className="button is-black">Mint</button>*/}
      {propertiesModalActive && (
        <PropertiesModal
          properties={propertiesModalActive}
          onClose={() => setPropertiesModalActive(undefined)}
        />
      )}
    </>
  );
};

export default Gallery;
