import React, { useEffect, useRef, useState } from "react";
import PageLayout from "../../components/PageLayout";
import { useWallet } from "../../../packages/provider";
import { SmithContract } from "../../../packages/neo/contracts/ftw/smith";
import ActionModal from "./ActionModal";
import ContractCard from "./ContractCard";
import WAVES from "vanta/dist/vanta.waves.min";

const Smith = (props) => {
  const [isLoading, setLoading] = useState(true);
  const [status, setStatus] = useState<any>([]);
  const [isActionModalActive, setActionModalActive] = useState(false);
  const [error, setError] = useState("");
  const { connectedWallet, network } = useWallet();
  const [vantaEffect, setVantaEffect] = useState(0);
  const myRef = useRef(null);

  useEffect(() => {
    async function fetchContractStatus() {
      setError("");
      try {
        const res = await new SmithContract(network).getStatus();
        setStatus(res);
        setLoading(false);
      } catch (e: any) {
        setError(e.message);
      }
    }
    fetchContractStatus();
    // if (!vantaEffect) {
    //   setVantaEffect(
    //     WAVES({
    //       el: myRef.current,
    //       mouseControls: true,
    //       touchControls: true,
    //       gyroControls: false,
    //       minHeight: 200.0,
    //       minWidth: 200.0,
    //       scale: 1.0,
    //       scaleMobile: 1.0,
    //       color: 0x97abbb,
    //     })
    //   );
    // }
    // return () => {
    //   if (vantaEffect) {
    //     // @ts-ignore
    //     vantaEffect.destroy();
    //   }
    // };
  }, [connectedWallet, vantaEffect]);

  return (
    <>
      <section className="hero is-white is-medium" ref={myRef}>
        <div className="hero-body">
          <div className="container">
            <h1 className="title">Forthewin Smith</h1>
            <p className="subtitle">
              Mint your NEP-17 tokens without any codes
            </p>
            <button
              onClick={() => setActionModalActive(true)}
              className="button is-primary press-font"
            >
              Mint Tokens
            </button>
          </div>
        </div>
      </section>
      <PageLayout>
        {isLoading ? (
          <div>Loading..</div>
        ) : error ? (
          <div>{error}</div>
        ) : (
          <div className="box">
            {status && status.records.items.length > 0 ? (
              status.records.items.map((item) => <ContractCard data={item} />)
            ) : (
              <div></div>
            )}
          </div>
        )}
        {isActionModalActive && (
          <ActionModal onClose={() => setActionModalActive(false)} />
        )}
      </PageLayout>
    </>
  );
};

export default Smith;
